// app/store/analytics/page.jsx
'use client'
import { useEffect, useState } from "react"
import { useAuth } from "@clerk/nextjs"
import axios from "axios"
import toast from "react-hot-toast"
import Loading from "@/components/Loading"
import { TrendingUp, TrendingDown, DollarSign, Package, ShoppingCart, Users } from "lucide-react"

export default function StoreAnalytics() {
    const { getToken } = useAuth()
    const currency = process.env.NEXT_PUBLIC_CURRENCY_SYMBOL || '$'
    
    const [loading, setLoading] = useState(true)
    const [analytics, setAnalytics] = useState(null)

    const fetchAnalytics = async () => {
        try {
            const token = await getToken()
            const { data } = await axios.get('/api/store/dashboard', {
                headers: { Authorization: `Bearer ${token}` }
            })
            setAnalytics(data.dashboardData)
        } catch (error) {
            toast.error(error?.response?.data?.error || error.message)
        }
        setLoading(false)
    }

    useEffect(() => {
        fetchAnalytics()
    }, [])

    if (loading) return <Loading />

    const calculateGrowth = () => {
        // Calculate revenue growth (mock calculation)
        const revenueData = Object.values(analytics.revenueByDate)
        if (revenueData.length < 2) return 0
        const recent = revenueData.slice(-7).reduce((a, b) => a + b, 0) / 7
        const previous = revenueData.slice(-14, -7).reduce((a, b) => a + b, 0) / 7
        return previous > 0 ? ((recent - previous) / previous * 100).toFixed(1) : 0
    }

    const growth = calculateGrowth()

    return (
        <div className="text-slate-500 mb-28">
            <h1 className="text-2xl mb-6">Store <span className="text-slate-800 font-medium">Analytics</span></h1>

            {/* Key Metrics */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
                <div className="bg-white border border-slate-200 rounded-lg p-5">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-sm text-slate-500">Total Revenue</p>
                            <p className="text-2xl font-bold text-slate-800 mt-1">
                                {currency}{analytics.totalEarnings.toLocaleString()}
                            </p>
                            <div className="flex items-center gap-1 mt-2">
                                {growth >= 0 ? (
                                    <TrendingUp className="text-green-500" size={16} />
                                ) : (
                                    <TrendingDown className="text-red-500" size={16} />
                                )}
                                <span className={`text-sm ${growth >= 0 ? 'text-green-500' : 'text-red-500'}`}>
                                    {Math.abs(growth)}%
                                </span>
                                <span className="text-xs text-slate-400">vs last week</span>
                            </div>
                        </div>
                        <div className="bg-green-100 p-3 rounded-full">
                            <DollarSign className="text-green-600" size={24} />
                        </div>
                    </div>
                </div>

                <div className="bg-white border border-slate-200 rounded-lg p-5">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-sm text-slate-500">Total Products</p>
                            <p className="text-2xl font-bold text-slate-800 mt-1">
                                {analytics.totalProducts}
                            </p>
                            <p className="text-xs text-slate-400 mt-2">Active listings</p>
                        </div>
                        <div className="bg-blue-100 p-3 rounded-full">
                            <Package className="text-blue-600" size={24} />
                        </div>
                    </div>
                </div>

                <div className="bg-white border border-slate-200 rounded-lg p-5">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-sm text-slate-500">Total Orders</p>
                            <p className="text-2xl font-bold text-slate-800 mt-1">
                                {analytics.totalOrders}
                            </p>
                            <p className="text-xs text-slate-400 mt-2">All time</p>
                        </div>
                        <div className="bg-purple-100 p-3 rounded-full">
                            <ShoppingCart className="text-purple-600" size={24} />
                        </div>
                    </div>
                </div>

                <div className="bg-white border border-slate-200 rounded-lg p-5">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-sm text-slate-500">Unique Customers</p>
                            <p className="text-2xl font-bold text-slate-800 mt-1">
                                {analytics.uniqueCustomers}
                            </p>
                            <p className="text-xs text-slate-400 mt-2">Total buyers</p>
                        </div>
                        <div className="bg-orange-100 p-3 rounded-full">
                            <Users className="text-orange-600" size={24} />
                        </div>
                    </div>
                </div>
            </div>

            {/* Order Status Breakdown */}
            <div className="bg-white border border-slate-200 rounded-lg p-6 mb-8">
                <h2 className="text-lg font-semibold text-slate-800 mb-4">Order Status</h2>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                    {Object.entries(analytics.ordersByStatus).map(([status, count]) => (
                        <div key={status} className="border rounded-lg p-4 text-center">
                            <p className="text-sm text-slate-500 mb-2">
                                {status.replace('_', ' ')}
                            </p>
                            <p className="text-3xl font-bold text-slate-800">{count}</p>
                        </div>
                    ))}
                </div>
            </div>

            {/* Category Performance */}
            <div className="bg-white border border-slate-200 rounded-lg p-6 mb-8">
                <h2 className="text-lg font-semibold text-slate-800 mb-4">Category Performance</h2>
                <div className="space-y-4">
                    {Object.entries(analytics.categoryStats).map(([category, stats]) => (
                        <div key={category} className="border-b pb-4 last:border-0">
                            <div className="flex justify-between items-center mb-2">
                                <div>
                                    <p className="font-medium text-slate-800">{category}</p>
                                    <p className="text-sm text-slate-500">{stats.products} products</p>
                                </div>
                                <div className="text-right">
                                    <p className="font-semibold text-green-600">
                                        {currency}{stats.revenue.toLocaleString()}
                                    </p>
                                    <p className="text-sm text-slate-500">{stats.totalSold} sold</p>
                                </div>
                            </div>
                            <div className="w-full bg-slate-100 rounded-full h-2">
                                <div 
                                    className="bg-green-500 h-2 rounded-full"
                                    style={{
                                        width: `${(stats.revenue / analytics.totalEarnings * 100)}%`
                                    }}
                                ></div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Payment Methods */}
            <div className="bg-white border border-slate-200 rounded-lg p-6 mb-8">
                <h2 className="text-lg font-semibold text-slate-800 mb-4">Payment Methods</h2>
                <div className="grid grid-cols-2 gap-4">
                    <div className="border rounded-lg p-4">
                        <p className="text-sm text-slate-500 mb-2">Cash on Delivery</p>
                        <p className="text-3xl font-bold text-slate-800 mb-2">
                            {analytics.paymentBreakdown.COD}
                        </p>
                        <div className="w-full bg-slate-100 rounded-full h-2">
                            <div 
                                className="bg-blue-500 h-2 rounded-full"
                                style={{
                                    width: `${(analytics.paymentBreakdown.COD / analytics.totalOrders * 100)}%`
                                }}
                            ></div>
                        </div>
                    </div>
                    <div className="border rounded-lg p-4">
                        <p className="text-sm text-slate-500 mb-2">Stripe</p>
                        <p className="text-3xl font-bold text-slate-800 mb-2">
                            {analytics.paymentBreakdown.STRIPE}
                        </p>
                        <div className="w-full bg-slate-100 rounded-full h-2">
                            <div 
                                className="bg-green-500 h-2 rounded-full"
                                style={{
                                    width: `${(analytics.paymentBreakdown.STRIPE / analytics.totalOrders * 100)}%`
                                }}
                            ></div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Average Order Value */}
            <div className="bg-white border border-slate-200 rounded-lg p-6">
                <h2 className="text-lg font-semibold text-slate-800 mb-4">Key Insights</h2>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <div className="text-center p-4 bg-blue-50 rounded-lg">
                        <p className="text-sm text-slate-600 mb-1">Average Order Value</p>
                        <p className="text-2xl font-bold text-blue-600">
                            {currency}{analytics.avgOrderValue}
                        </p>
                    </div>
                    <div className="text-center p-4 bg-green-50 rounded-lg">
                        <p className="text-sm text-slate-600 mb-1">Conversion Rate</p>
                        <p className="text-2xl font-bold text-green-600">
                            {((analytics.totalOrders / analytics.uniqueCustomers) * 100).toFixed(1)}%
                        </p>
                    </div>
                    <div className="text-center p-4 bg-purple-50 rounded-lg">
                        <p className="text-sm text-slate-600 mb-1">Revenue Per Product</p>
                        <p className="text-2xl font-bold text-purple-600">
                            {currency}{(analytics.totalEarnings / analytics.totalProducts).toFixed(2)}
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}